import React from "react";
import "./main.css";
import Empty from "../Empty/empty";
import ChatWindow from "../../containers/ChatWindow/ChatWindow"


class Main extends React.Component{
  constructor(props){
    super(props);
    this.mainRef= React.createRef()
  }
  componentDidMount() {
    this.scrollToBottom();
  }
  componentDidUpdate() {
    this.scrollToBottom();
  }
  scrollToBottom = () => {
    this.mainRef.current.scrollTop = this.mainRef.current.scrollHeight;
  }

  renderMainContent(){
    if (!this.props.activeUserId){
      return <Empty user={this.props.user} activeUserId={this.props.activeUserId} />
    }else {
      return <ChatWindow activeUserId={this.props.activeUserId}/>
    }
  }
  render(){
    return(
      <main className="Main" ref={this.mainRef}>{this.renderMainContent()}</main>
    )
  }

}

export default Main;
